<style lang="scss" scoped>
.report {
  padding: 30px;
  &_form {
    margin-left: 50px;
  }
}
</style>

<template>
  <div class="report">
    <el-form ref="form" class="report_form" :model="form" label-width="100px">
      <el-form-item label="报告名称">
        <el-input v-model="form.reportName" style="width:400px;"></el-input>
      </el-form-item>
      <el-form-item label="缺陷总数">
        <el-input-number v-model="form.total" :min="0"></el-input-number>
      </el-form-item>
      <el-form-item label="已关闭缺陷数">
        <el-input-number v-model="form.closed" :min="0"></el-input-number>
      </el-form-item>
      <el-form-item label="残留缺陷数">
        <el-input-number v-model="form.remain" :min="0"></el-input-number>
      </el-form-item>
      <el-form-item label="解决率">
        <el-input v-model="form.probability" style="width:150px;">
          <template slot="append">%</template>
        </el-input>
      </el-form-item>
      <el-form-item label="新增功能">
        <el-input
          placeholder="迭代新增功能点"
          type="textarea"
          style="width:800px;"
          :autosize="{ minRows: 6}"
          v-model="form.new"
        ></el-input>
      </el-form-item>
      <el-form-item label="修改模块">
        <el-input
          placeholder="开发重点修改的模块"
          type="textarea"
          style="width:800px;"
          :autosize="{ minRows: 6}"
          v-model="form.change"
        ></el-input>
      </el-form-item>
      <el-form-item label="重点测试">
        <el-input
          placeholder="需重点测试或可能会出现bug的地方"
          type="textarea"
          style="width:800px;"
          :autosize="{ minRows: 6}"
          v-model="form.important"
        ></el-input>
      </el-form-item>
      <el-form-item label="BUG汇总">
        <el-button size="mini" type="primary" @click="addBug">新增bug描述</el-button>
        <el-table :data="form.bugList" border style="width: 100%">
          <el-table-column prop="description" label="BUG描述"></el-table-column>
          <el-table-column prop="reason" label="产生原因"></el-table-column>
          <el-table-column prop="information" label="修复情况"></el-table-column>
          <el-table-column fixed="right" label="操作" width="100">
            <template slot-scope="scope">
              <el-button @click="deleteBug(scope.row,scope.$index)" type="warning" size="mini">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">立即创建</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
    <el-dialog
      width="600px"
      title="新增/编辑"
      :visible.sync="dialogFormVisible"
      :close-on-click-modal="false"
    >
      <el-form
        ref="dataForm"
        :rules="rules"
        :model="editData"
        label-position="left"
        label-width="90px"
        style="width: 400px; margin-left:50px;"
      >
        <el-form-item label="BUG描述" prop="description">
          <el-input v-model="editData.description"/>
        </el-form-item>
        <el-form-item label="产生原因" prop="reason">
          <el-input v-model="editData.reason"/>
        </el-form-item>
        <el-form-item label="修复情况" prop="information">
          <el-input v-model="editData.information"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button  @click="dialogFormVisible=false">取消</el-button>
        <el-button type="primary" @click="submitData('dataForm')">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
  <script>
import { mapGetters } from "vuex";
import { Report } from "@/api/report";
export default {
  data() {
    return {
      dialogFormVisible: false,
      rules: {
        description: [
          { required: true, message: "请填写BUG描述", trigger: "blur" }
        ],
        reason: [
          { required: true, message: "请填写产生原因", trigger: "blur" }
        ],
        information: [
          { required: true, message: "请选择修复情况", trigger: "blur" }
        ]
      },
      form: {
        reportName: "",
        total: "0",
        closed: "0",
        remain: "0",
        probability: "99",
        new: "",
        change: "",
        important: "",
        bugList: []
      },
      editData: {
        description: "",
        reason: "",
        information: ""
      },
      id:null,
    };
  },
  computed: {
    ...mapGetters(["name", "userId"])
  },
  created(){
      this.id=this.$route.query.id;
    if (this.id) {
        this.getData(this.id);
    }
  },
  methods: {
      getData(id){
          Report.getOne({
           id   
          }).then(res=>{
              this.form=res.data;
          })
      },
    onSubmit() {
      this.$confirm("确定发布?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          Report.create({
            ...this.form,
            name: this.name,
            userId: this.userId,
          }).then(res => {
            this.$message({
              type: "success",
              message: "发布成功"
            });
            this.$router.push('/report/list');
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消"
          });
        });
    },
    addBug() {
      this.editData = {};
      this.dialogFormVisible = true;
    },
    submitData(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.form.bugList.push(JSON.parse(JSON.stringify(this.editData)));
          this.dialogFormVisible = false;
        } else {
          return false;
        }
      });
    },
    deleteBug(obj, index) {
      this.$confirm("确定删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.form.bugList.splice(index, 1);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  }
};
</script>