import fetchobj from '@/utils/fetch'
let fetch = fetchobj.fetch;

export const Report = {
   
  create(data) {
    return fetch('post', '/report', data)
  },

  get(data) {
    return fetch('get', '/report', data)
  },

  getOne(data) {
    return fetch('get', '/oneReport', data)
  },

  delete(data){
    return fetch('delete', '/report', data)
  },

  update(data){
    return fetch('put', '/report', data)
  }

}
